<template>
  <tas-base-crud :config="config" />
</template>

<script>
export default {
  name: 'crud-company-organization-structures',
  data () {
    return {
      config: {
        title: 'Struktur Organisasi Perusahaan',
        model_api: 'company-organization-structures',
        getter: 'company-organization-structures',
        setter: 'company-organization-structures',
        pk_field: null,
        permission: {
          create: 'create-company-organization-structures',
          read: 'view-company-organization-structures',
          update: 'update-company-organization-structures',
          delete: 'delete-company-organization-structures'
        },
        slave: [],
        redirectAfterForm: { name: 'perusahaan' },
        fields: [
          { id: 'id', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'updated_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_by', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'created_at', label: 'Dibuat', methods: { create: false, update: false, filter: false } },
          { id: 'updated_at', label: 'Diperbarui', methods: { list: false, detail: false, create: false, update: false, filter: false } },
          { id: 'description', label: 'Deskripsi', methods: { create: { type: 'textarea' }, update: { type: 'textarea' } } },
          { id: 'periode_year', label: 'Periode Tahun', methods: { list: { transform: 'year' }, detail: { transform: 'year' }, create: { type: 'year' }, update: { type: 'year' }, filter: false } },
          {
            id: 'img_photo',
            label: 'File Struktur',
            methods: {
              list: false,
              detail: {
                type: 'file',
                view_data: 'img_photo.url'
              },
              create: {
                type: 'file',
                attr: [{ accept: 'image/*, .pdf' }, { limit: 5 }]
              },
              update: {
                type: 'file',
                attr: [{ accept: 'image/*, .pdf' }, { limit: 5 }],
                view_data: 'img_photo'
              }
            }
          }
        ]
      }
    }
  }
}
</script>



